.learn-more3,
.property-1default {
  display: flex;
  align-items: center;
  justify-content: center;
}
.learn-more3 {
  width: 131.5px;
  position: relative;
  letter-spacing: 0.05em;
  flex-shrink: 0;
}
.property-1default {
  position: absolute;
  top: calc(50% - 130.5px);
  left: calc(50% - 156.5px);
  border-radius: var(--br-8xs);
  background-color: var(--color-darkolivegreen);
  border: 1px solid var(--color-white);
  box-sizing: border-box;
  width: 150px;
  height: 45px;
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-xl);
}
.learn-more4 {
  position: relative;
  letter-spacing: 0.05em;
  font-weight: 700;
  color: inherit;
  text-decoration: none;
}
.learn-more-poverty,
.property-1variant2 {
  border-radius: var(--br-8xs);
  box-sizing: border-box;
  overflow: hidden;
}
.property-1variant2 {
  position: absolute;
  top: calc(50% + 34px);
  left: calc(50% - 151.5px);
  background-color: var(--color-white);
  border: 4px solid var(--color-white);
  width: 150px;
  height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs) var(--padding-xl);
  color: var(--color-mediumseagreen-200);
}
.learn-more-poverty {
  width: 100%;
  position: relative;
  border: 1px dashed var(--color-blueviolet);
  height: 336px;
  text-align: center;
  font-size: var(--font-size-base);
  color: var(--color-white);
  font-family: var(--font-arial);
}
